.store-header {
  display: flex;
  flex-direction: column;
  background: var(--glass-bg, rgba(255, 255, 255, 0.75));
  backdrop-filter: var(--glass-blur, blur(20px));
  -webkit-backdrop-filter: var(--glass-blur, blur(20px));
  border-bottom: 1px solid var(--tile-border, rgba(255, 255, 255, 0.4));
  box-shadow: var(--tile-shadow, 0 4px 30px rgba(0, 0, 0, 0.05));
  width: 100%;
  min-height: 96px;
  transition: all 0.3s ease;
  position: sticky;
  top: 0;
  z-index: 1000;
}

  .top-menu-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 56px;
    padding: 0 20px;
    background: transparent;
    color: var(--text-primary, #1d1d1f);

    .header-left {
      display: flex;
      align-items: center;
      gap: 16px;
      flex: 1;
      min-width: 0;  // flex 자식이 줄어들 수 있도록
      overflow: hidden;  // 넘치는 콘텐츠 방지

      .menu-toggle-btn {
        background: none;
        border: none;
        color: var(--text-primary, #1d1d1f);
        cursor: pointer;
        padding: 8px;
        border-radius: 4px;
        transition: background-color 0.2s;
        flex-shrink: 0;  // 버튼은 줄어들지 않음

        &:hover {
          background: var(--bg-hover, rgba(0, 0, 0, 0.05));
        }

        mat-icon {
          font-size: 24px;
          width: 24px;
          height: 24px;
        }
      }

      .store-logo {
        display: flex;
        align-items: center;
        gap: 8px;
        min-width: 0;  // flex 자식이 줄어들 수 있도록
        flex: 1;  // 사용 가능한 공간 사용
        cursor: pointer;
        padding: 8px 12px;
        margin: -8px -12px;
        border-radius: 8px;
        transition: background-color 0.2s ease;

        &:hover {
          background: var(--bg-hover, rgba(0, 0, 0, 0.05));
        }

        &:active {
          background: var(--bg-hover, rgba(0, 0, 0, 0.1));
        }

        .logo-icon {
          font-size: 28px;
          width: 28px;
          height: 28px;
          color: var(--accent-primary, #007aff);
          flex-shrink: 0;  // 아이콘은 줄어들지 않음
        }

        h1 {
          margin: 0;
          font-size: 20px;
          font-weight: 600;
          letter-spacing: -0.5px;
          white-space: nowrap;  // 한 줄로 표시
          overflow: hidden;  // 넘치는 텍스트 숨김
          text-overflow: ellipsis;  // ... 표시
          min-width: 0;  // flex 줄어들기 허용
          color: var(--text-primary, #1d1d1f);
        }
      }

      .store-stats {
        display: flex;
        align-items: center;
        gap: 4px;
        background: var(--bg-secondary, rgba(0, 0, 0, 0.05));
        padding: 4px 12px;
        border-radius: 16px;
        font-size: 12px;
        flex-shrink: 0;  // stats는 줄어들지 않음
        white-space: nowrap;  // 한 줄로 유지

        .stats-label {
          opacity: 0.8;
          color: var(--text-secondary, #333);
        }

        .stats-count {
          font-weight: 600;
          color: var(--accent-primary, #007aff);
        }
      }
    }  .header-center {
    flex: 2;
    display: flex;
    justify-content: center;
    max-width: 500px;

    .search-container {
      display: flex;
      align-items: center;
      background: var(--bg-secondary, rgba(0, 0, 0, 0.05));
      border-radius: 24px;
      padding: 8px 16px;
      width: 100%;
      transition: all 0.3s ease;
      border: 1px solid var(--tile-border, transparent);

      &:hover,
      &.active {
        background: var(--bg-hover, rgba(0, 0, 0, 0.08));
        border-color: var(--accent-primary, rgba(0, 0, 0, 0.1));
      }

      .search-icon {
        font-size: 20px;
        width: 20px;
        height: 20px;
        margin-right: 8px;
        opacity: 0.6;
        color: var(--text-primary, #1d1d1f);
      }

      .search-input {
        flex: 1;
        background: none;
        border: none;
        color: var(--text-primary, #1d1d1f);
        font-size: 14px;
        outline: none;

        &::placeholder {
          color: var(--text-secondary, rgba(0, 0, 0, 0.5));
        }
      }

      .search-btn {
        background: var(--bg-tertiary, rgba(0, 0, 0, 0.05));
        border: none;
        color: var(--text-primary, #1d1d1f);
        padding: 6px 12px;
        border-radius: 12px;
        font-size: 12px;
        cursor: pointer;
        transition: background-color 0.2s;

        &:hover:not(:disabled) {
          background: var(--bg-hover, rgba(0, 0, 0, 0.1));
        }

        &:disabled {
          opacity: 0.5;
          cursor: not-allowed;
        }
      }
    }
  }

  .header-right {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 12px;

    // 인증 섹션 스타일
    .auth-section {
      display: flex;
      align-items: center;
      gap: 12px;

      .user-info {
        display: flex;
        align-items: center;
        gap: 10px;
        padding: 6px 12px;
        border-radius: 10px;
        background: var(--tile-bg, #f8f9fa);
        border: 1px solid var(--tile-border, #e9ecef);
        transition: all 0.2s ease;
        cursor: pointer;

        &:hover {
          background: var(--bg-hover, rgba(0, 0, 0, 0.05));
          box-shadow: var(--tile-shadow, 0 4px 12px rgba(0, 0, 0, 0.08));
          border-color: var(--accent-primary);
        }

        :global(.dark-theme) & {
          background: var(--tile-bg, #334155) !important;
          border-color: var(--tile-border, #475569) !important;

          &:hover {
            background: var(--bg-hover, rgba(255, 255, 255, 0.1)) !important;
          }

          .user-name {
            color: var(--text-primary, #f1f5f9) !important;
          }

          .user-email {
            color: var(--text-secondary, #cbd5e1) !important;
          }

          .dropdown-icon {
            color: var(--text-secondary, #cbd5e1) !important;
          }
        }

        .user-avatar {
          width: 34px;
          height: 34px;
          border-radius: 50%;
          background: var(--bg-secondary, #eef2f7);
          color: var(--accent-primary, #007aff);
          display: flex;
          align-items: center;
          justify-content: center;
          flex-shrink: 0;
          border: 1px solid var(--tile-border, rgba(0, 122, 255, 0.1));

          :global(.dark-theme) & {
            background: var(--bg-secondary, #1e293b);
            border-color: var(--accent-primary, #818cf8);
          }

          mat-icon {
            font-size: 22px;
            width: 22px;
            height: 22px;

            :global(.dark-theme) & {
              color: var(--accent-primary, #818cf8);
            }
          }
        }

        .user-details {
          display: flex;
          flex-direction: column;
          gap: 1px;
          min-width: 0;

          .user-name-row {
            display: flex;
            align-items: center;
            gap: 6px;

            .user-name {
              font-size: 13px;
              font-weight: 700;
              color: var(--text-primary, #1d1d1f);
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
              max-width: 120px;

              :global(.dark-theme) & {
                color: var(--text-primary, #f1f5f9);
              }
            }

            .user-mode-badge {
              font-size: 9px;
              font-weight: 600;
              color: var(--accent-primary, #007aff);
              background: var(--bg-accent, rgba(0, 122, 255, 0.08));
              padding: 1px 6px;
              border-radius: 4px;
              white-space: nowrap;

              :global(.dark-theme) & {
                color: #a5b4fc;
                background: rgba(129, 140, 248, 0.15);
              }
            }
          }

          .user-email {
            font-size: 11px;
            color: var(--text-secondary, #6c757d);
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            max-width: 150px;

            :global(.dark-theme) & {
              color: var(--text-secondary, #cbd5e1);
            }
          }
        }

        .dropdown-icon {
          font-size: 18px;
          width: 18px;
          height: 18px;
          color: var(--text-secondary, #86868b);
          flex-shrink: 0;

          :global(.dark-theme) & {
            color: var(--text-secondary, #cbd5e1);
          }
        }
      }

      .login-section {
        display: flex;
        align-items: center;
        gap: 8px;

        .current-mode {
          font-size: 11px;
          color: var(--text-secondary, #666);
          margin-right: 4px;

          :global(.dark-theme) & {
            color: var(--text-secondary, #cbd5e1);
          }
        }

        .login-btn {
          background: var(--bg-secondary, rgba(0, 0, 0, 0.05));
          border: 1px solid var(--tile-border, rgba(0, 0, 0, 0.1));
          color: var(--text-primary, #1d1d1f);
          padding: 6px 12px;
          border-radius: 6px;
          font-size: 12px;
          font-weight: 500;
          cursor: pointer;
          display: flex;
          align-items: center;
          gap: 4px;
          transition: all 0.2s ease;

          :global(.dark-theme) & {
            background: var(--bg-tertiary, #334155);
            border-color: var(--border-color, #475569);
            color: var(--text-primary, #f1f5f9);

            &:hover {
              background: var(--glass-bg, rgba(30, 41, 59, 0.8));
              border-color: var(--accent-primary, #818cf8);
            }
          }

          &:hover {
            background: var(--bg-hover, rgba(0, 0, 0, 0.1));
            transform: translateY(-1px);
          }

          mat-icon {
            font-size: 14px;
            width: 14px;
            height: 14px;
          }
        }

        .mode-switch-btn {
          background: var(--bg-secondary, rgba(0, 0, 0, 0.05));
          border: 1px solid var(--tile-border, rgba(0, 0, 0, 0.1));
          color: var(--text-primary, #1d1d1f);
          padding: 6px;
          border-radius: 6px;
          cursor: pointer;
          transition: all 0.2s ease;

          :global(.dark-theme) & {
            background: var(--bg-tertiary, #334155);
            border-color: var(--border-color, #475569);
            color: var(--text-primary, #f1f5f9);

            &:hover {
              background: var(--glass-bg, rgba(30, 41, 59, 0.8));
              border-color: var(--accent-primary, #818cf8);
            }
          }

          &:hover {
            background: var(--bg-hover, rgba(0, 0, 0, 0.1));
          }

          mat-icon {
            font-size: 16px;
            width: 16px;
            height: 16px;
          }
        }
      }
    }

    .toolbar-actions {
      display: flex;
      gap: 8px;
      align-items: center;

      .toolbar-btn {
        background: none;
        border: none;
        color: var(--text-primary, #1d1d1f);
        cursor: pointer;
        padding: 8px;
        border-radius: 4px;
        transition: all 0.2s ease;
        position: relative;

        &:hover:not(:disabled) {
          background: var(--bg-hover, rgba(0, 0, 0, 0.05));
        }

        &:disabled {
          opacity: 0.5;
          cursor: not-allowed;
        }

        &.theme-toggle {
          position: relative;
          
          &:hover {
            background: var(--bg-hover, rgba(0, 0, 0, 0.05));
            transform: scale(1.05);
          }
        }

        // 채팅 버튼 특별 스타일
        &.chat-btn {
          &:hover {
            background: var(--bg-hover, rgba(0, 0, 0, 0.05));
            transform: scale(1.05);
          }

          ::ng-deep .mat-badge-content {
            background: var(--error-color, #ff4444);
            color: white;
            font-size: 10px;
            font-weight: 600;
          }
        }

        mat-icon {
          font-size: 20px;
          width: 20px;
          height: 20px;
        }
      }
    }
  }
}

.nav-menu {
  height: 40px;
  background: var(--glass-bg, rgba(255, 255, 255, 0.5));
  backdrop-filter: var(--glass-blur, blur(10px));
  -webkit-backdrop-filter: var(--glass-blur, blur(10px));
  border-top: 1px solid var(--tile-border, rgba(255, 255, 255, 0.4));

  .nav-items {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0 20px;
    gap: 8px;

    .nav-item {
      display: flex;
      align-items: center;
      gap: 6px;
      background: none;
      border: none;
      color: var(--text-secondary, #666);
      cursor: pointer;
      padding: 8px 16px;
      border-radius: 6px;
      font-size: 13px;
      font-weight: 500;
      transition: all 0.2s ease;

      &:hover {
        background: var(--bg-hover, rgba(0, 0, 0, 0.05));
        color: var(--text-primary, #1d1d1f);
      }

      &.active {
        background: var(--tile-bg, rgba(255, 255, 255, 0.8));
        color: var(--accent-primary, #007aff);
        font-weight: 600;
        border: 1px solid var(--tile-border, rgba(0, 0, 0, 0.05));
        box-shadow: var(--tile-shadow, 0 2px 8px rgba(0, 0, 0, 0.05));
      }

      mat-icon {
        font-size: 16px;
        width: 16px;
        height: 16px;
      }
    }
  }
}

// 반응형 디자인
@media (max-width: 1024px) {
  .top-menu-bar {
    padding: 0 16px;

    .header-center {
      max-width: 300px;
    }
  }
}

@media (max-width: 768px) {
  .store-header {
    min-height: 80px;
  }

  .top-menu-bar {
    height: 48px;
    padding: 0 12px;

    .header-left {
      gap: 12px;

      .store-logo h1 {
        font-size: 16px;
      }

      .store-stats {
        display: none;
      }
    }

    .header-center {
      .search-container {
        padding: 6px 12px;

        .search-input {
          font-size: 12px;
        }

        .search-btn {
          padding: 4px 8px;
          font-size: 11px;
        }
      }
    }
  }

  .nav-menu {
    height: 32px;

    .nav-items {
      padding: 0 12px;
      gap: 4px;

      .nav-item {
        padding: 4px 8px;
        font-size: 11px;

        span {
          display: none;
        }

        mat-icon {
          font-size: 14px;
          width: 14px;
          height: 14px;
        }
      }
    }
  }
}

@media (max-width: 480px) {
  .top-menu-bar {
    .header-left .store-logo h1 {
      font-size: 14px;
    }

    .header-center {
      flex: 1.5;
    }

    .header-right .toolbar-actions {
      gap: 4px;

      .toolbar-btn {
        padding: 6px;
      }
    }
  }

  .nav-menu .nav-items {
    justify-content: space-around;
    gap: 0;

    .nav-item {
      padding: 4px;
      min-width: 32px;
      justify-content: center;
    }
  }
}

/* 애니메이션 */
@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

// 메뉴 스타일 제거 (src/styles.scss로 이동)
// ==========================================
// 모바일 최적화 (768px 이하)
// ==========================================
@media (max-width: 768px) {
  .store-header {
    min-height: 56px;
  }

  .top-menu-bar {
    height: 56px;
    padding: 0 12px;

    .header-left {
      gap: 8px;
      flex: 0 0 auto;

      .menu-toggle-btn {
        padding: 6px;

        mat-icon {
          font-size: 22px;
          width: 22px;
          height: 22px;
        }
      }

      .store-logo {
        h1 {
          font-size: 16px;
        }

        .logo-icon {
          font-size: 20px;
          width: 20px;
          height: 20px;
        }
      }
    }

    .header-center {
      display: none;
    }

    .header-right {
      gap: 4px;

      .theme-toggle {
        padding: 6px;

        mat-icon {
          font-size: 20px;
          width: 20px;
          height: 20px;
        }

        span {
          display: none;
        }
      }

      .cart-button {
        padding: 6px;
        min-width: auto;

        mat-icon {
          font-size: 22px;
          width: 22px;
          height: 22px;
        }

        span {
          display: none;
        }

        .cart-badge {
          top: 2px;
          right: 2px;
          min-width: 16px;
          height: 16px;
          font-size: 10px;
          padding: 0 4px;
        }
      }

      .auth-section {
        .user-info {
          padding: 4px 8px;
          gap: 6px;
          background: var(--tile-bg, #f8f9fa);
          border: 1px solid var(--tile-border, #e9ecef);

          :global(.dark-theme) & {
            background: var(--tile-bg, #334155) !important;
            border-color: var(--tile-border, #475569) !important;
          }

          .user-avatar {
            width: 32px;
            height: 32px;

            :global(.dark-theme) & {
              background: var(--bg-secondary, #1e293b);
              border-color: var(--accent-primary, #818cf8);
            }

            mat-icon {
              font-size: 24px;
              width: 24px;
              height: 24px;

              :global(.dark-theme) & {
                color: var(--accent-primary, #818cf8);
              }
            }
          }

          .user-details {
            display: flex; // 모바일에서도 정보 표시
            
            .user-name-row {
              .user-name {
                max-width: 70px; // 모바일에서는 더 짧게
                font-size: 12px;

                :global(.dark-theme) & {
                  color: var(--text-primary, #f1f5f9);
                }
              }
              .user-mode-badge {
                display: none; // 모바일에서는 배지 숨김
              }
            }

            .user-email {
              display: none; // 모바일에서는 이메일 숨김
            }
          }

          .dropdown-icon {
            display: none;
          }
        }

        .login-section {
          gap: 4px;

          .login-btn {
            padding: 6px 12px;
            font-size: 13px;
            min-width: auto;

            :global(.dark-theme) & {
              background: var(--bg-tertiary, #334155);
              border-color: var(--border-color, #475569);
              color: var(--text-primary, #f1f5f9);

              &:hover {
                background: var(--glass-bg, rgba(30, 41, 59, 0.8));
                border-color: var(--accent-primary, #818cf8);
              }
            }

            mat-icon {
              font-size: 18px;
              width: 18px;
              height: 18px;
            }

            span {
              display: none;
            }
          }
        }
      }
    }
  }

  .menu-bar {
    display: none;
  }

  .user-menu {
    .mat-mdc-menu-content {
      min-width: 260px;
      max-width: 90vw;
    }
  }
}

@media (max-width: 480px) {
  .top-menu-bar {
    padding: 0 8px;

    .header-left {
      gap: 6px;

      .store-logo {
        h1 {
          font-size: 14px;
        }

        .logo-icon {
          font-size: 18px;
          width: 18px;
          height: 18px;
        }
      }
    }
  }
}

// 모바일 검색 버튼 (기본적으로 숨김)
.mobile-search-btn {
  display: none;
}

// 모바일에서만 표시
@media (max-width: 768px) {
  .mobile-search-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    border: none;
    color: white;
    cursor: pointer;
    padding: 6px;
    border-radius: 4px;
    transition: background-color 0.2s;

    &:hover {
      background: rgba(255, 255, 255, 0.1);
    }

    mat-icon {
      font-size: 22px;
      width: 22px;
      height: 22px;
    }
  }
}
